
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
components: {
Swiper,
SwiperItem
},
data () {
return {
swiper: []
}
},
mounted () {
this.getSwiper({'type': 1})
},
methods: {
getSwiper (params) {
return this.$api.advertisement(params).then(res => {
this.swiper = res.data || []
})
}
}
}



